"use client";
import React, { useMemo, useState } from "react";
import {
    Table,
    TableHeader,
    TableBody,
    TableRow,
    TableCell,
} from "@/components/ui/table";
import Badge from "@/components/ui/badge/Badge";
import { Modal } from "@/components/ui/modal";

type TicketStatus =
    | "Draft"
    | "Submitted"
    | "In Progress"
    | "On Hold"
    | "Resolved"
    | "Rejected";

type Priority = "Low" | "Medium" | "High" | "Critical";

interface Attachment {
    name: string;
    url: string;
}

interface TimelineEntry {
    status: TicketStatus;
    user: string;
    timestamp: string;
}

interface Ticket {
    id: number;
    summary: string;
    description: string;
    status: TicketStatus;
    priority: Priority;
    assignmentGroup: AssignmentGroup;
    assignedTo: string;
    caller: string;
    ciAsset: string;
    category: string;
    subcategory: string;
    source: "Web" | "Chat" | "Integration" | "Email";
    createdAt: string;
    attachments: Attachment[];
    timeline: TimelineEntry[];
}

const GROUPS = {
    IT: ["John", "Alice"],
    HR: ["Priya"],
    Finance: ["David"],
};
type AssignmentGroup = keyof typeof GROUPS;


const CI_ASSETS = ["Laptop", "VPN", "Email", "Payroll"];
const STATUS_OPTIONS: TicketStatus[] = [
    "Draft",
    "Submitted",
    "In Progress",
    "On Hold",
    "Resolved",
    "Rejected",
];

export default function HelpDeskPage() {
    const [tickets, setTickets] = useState<Ticket[]>([]);
    const [selected, setSelected] = useState<Ticket | null>(null);
    const [open, setOpen] = useState(false);

    const [filters, setFilters] = useState({
        status: "",
        ci: "",
        month: "",
    });

    const filtered = useMemo(() => {
        return tickets.filter((t) => {
            const date = new Date(t.createdAt);
            const monthMatch = filters.month
                ? date.getMonth() + 1 === Number(filters.month)
                : true;
            return (
                (!filters.status || t.status === filters.status) &&
                (!filters.ci || t.ciAsset === filters.ci) &&
                monthMatch
            );
        });
    }, [tickets, filters]);
    return (
        <div className="space-y-6 p-6">
            <div className="flex justify-between items-center">
                <h1 className="text-xl font-semibold">Help Desk</h1>
                <button
                    onClick={() => {
                        setSelected(null);
                        setOpen(true);
                    }}
                    className="rounded bg-brand-600 px-4 py-2 text-white"
                >
                    New Ticket
                </button>
            </div>

            {/* Filters */}
            <div className="flex gap-4">
                <select
                    className="border px-3 py-2 rounded"
                    value={filters.status}
                    onChange={(e) => setFilters({ ...filters, status: e.target.value })}
                >
                    <option value="">All Status</option>
                    {STATUS_OPTIONS.map((s) => (
                        <option key={s}>{s}</option>
                    ))}
                </select>

                <select
                    className="border px-3 py-2 rounded"
                    value={filters.ci}
                    onChange={(e) => setFilters({ ...filters, ci: e.target.value })}
                >
                    <option value="">All CI</option>
                    {CI_ASSETS.map((c) => (
                        <option key={c}>{c}</option>
                    ))}
                </select>

                <select
                    className="border px-3 py-2 rounded"
                    value={filters.month}
                    onChange={(e) => setFilters({ ...filters, month: e.target.value })}
                >
                    <option value="">Month</option>
                    {[...Array(12)].map((_, i) => (
                        <option key={i} value={i + 1}>
                            {new Date(2024, i).toLocaleString("default", { month: "long" })}
                        </option>
                    ))}
                </select>
            </div>

            <div className="rounded-xl border bg-white">
                <Table>
                    <TableHeader>
                        <TableRow>
                            <TableCell isHeader>ID</TableCell>
                            <TableCell isHeader>Summary</TableCell>
                            <TableCell isHeader>Status</TableCell>
                            <TableCell isHeader>Priority</TableCell>
                            <TableCell isHeader>Group</TableCell>
                            <TableCell isHeader>CI</TableCell>
                        </TableRow>
                    </TableHeader>

                    <TableBody>
                        {filtered.map((t) => (
                            <TableRow key={t.id} className="hover:bg-gray-50">
                                <TableCell>{t.id}</TableCell>
                                <TableCell>
                                    <div
                                        className="cursor-pointer text-blue-600 font-medium hover:underline"
                                        onClick={() => {
                                            setSelected(t);
                                            setOpen(true);
                                        }}
                                    >
                                        {t.summary}
                                    </div>
                                </TableCell>
                                <TableCell>
                                    <Badge
                                        size="sm"
                                        color={
                                            t.status === "Resolved"
                                                ? "success"
                                                : t.status === "Rejected"
                                                    ? "error"
                                                    : t.status === "On Hold"
                                                        ? "info"
                                                        : t.status === "Submitted"
                                                            ? "warning"
                                                            : "secondary"
                                        }
                                    >
                                        {t.status}
                                    </Badge>
                                </TableCell>
                                <TableCell>{t.priority}</TableCell>
                                <TableCell>{t.assignmentGroup}</TableCell>
                                <TableCell>{t.ciAsset}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </div>
            <Modal isOpen={open} onClose={() => setOpen(false)} className="max-w-3xl">
                <TicketForm
                    ticket={selected}
                    onSave={(ticket) => {
                        setTickets((prev) =>
                            prev.some((p) => p.id === ticket.id)
                                ? prev.map((p) => (p.id === ticket.id ? ticket : p))
                                : [...prev, ticket]
                        );
                        setOpen(false);
                    }}
                />
            </Modal>
        </div>
    );
}
function TicketForm({
    ticket,
    onSave,
}: {
    ticket: Ticket | null;
    onSave: (t: Ticket) => void;
}) {
    const isNew = !ticket;
    const [form, setForm] = useState<Ticket>(
        ticket ?? {
            id: Date.now(),
            summary: "",
            description: "",
            status: "Draft",
            priority: "Medium",
            assignmentGroup: "IT",
            assignedTo: "",
            caller: "Current User",
            ciAsset: "",
            category: "IT",
            subcategory: "",
            source: "Web",
            createdAt: new Date().toISOString(),
            attachments: [],
            timeline: [],
        }
    );

    const users = GROUPS[form.assignmentGroup] || [];

    const updateStatus = (newStatus: TicketStatus) => {
        if (newStatus !== form.status) {
            setForm({
                ...form,
                status: newStatus,
                timeline: [
                    ...form.timeline,
                    {
                        status: newStatus,
                        user: "Current User",
                        timestamp: new Date().toISOString(),
                    },
                ],
            });
        }
    };
    return (
        <div className="space-y-4">
            <h2 className="text-lg font-semibold">
                {isNew ? "New Ticket" : `Ticket #${form.id}`}
            </h2>

            <input
                className="w-full border rounded px-3 py-2"
                placeholder="Summary"
                value={form.summary}
                onChange={(e) => setForm({ ...form, summary: e.target.value })}
            />

            <textarea
                className="w-full border rounded px-3 py-2"
                placeholder="Description"
                value={form.description}
                onChange={(e) => setForm({ ...form, description: e.target.value })}
            />

            <select
                className="w-full border rounded px-3 py-2"
                value={form.status}
                onChange={(e) => updateStatus(e.target.value as TicketStatus)}
            >
                {STATUS_OPTIONS.map((s) => (
                    <option key={s}>{s}</option>
                ))}
            </select>

            <input
                type="file"
                multiple
                onChange={(e) =>
                    setForm({
                        ...form,
                        attachments: [
                            ...form.attachments,
                            ...Array.from(e.target.files || []).map((f) => ({
                                name: f.name,
                                url: URL.createObjectURL(f),
                            })),
                        ],
                    })
                }
            />

            {/* Timeline */}
            <div className="border-l pl-4 space-y-3">
                {form.timeline.map((t, i) => (
                    <div key={i}>
                        <div className="font-medium">{t.status}</div>
                        <div className="text-xs text-gray-500">
                            {t.user} Â· {new Date(t.timestamp).toLocaleString()}
                        </div>
                    </div>
                ))}
            </div>

            <div className="flex justify-end gap-3">
                <button
                    onClick={() => onSave(form)}
                    className="rounded bg-brand-600 px-4 py-2 text-white"
                >
                    Save
                </button>
            </div>
        </div>
    );
}
